import React, { useState, useEffect } from 'react';
import Navigate from './Navigate';
import Product from './Product';
import { connect } from 'react-redux';
import * as actions from '../../actions';
import { useHistory } from 'react-router-dom';


function Dashboard(props) {
    let history = useHistory();

    let routeChange = ()=>{
            let path = `login`;
            history.push(path);
           

    };
    useEffect(() => {
       try{
        if(props.user.userId)
            console.log('signed');
       } catch(e){
           routeChange();
       }
       
           
      }, []);

    const [mode, setMode] = useState('all');

    let generateClass = (name)=>{
        console.log(mode)
        console.log(name)
        if(mode === name)
            return 'blue text-white';
        else    
            return 'grey'
       
    }

    return (
        
        <div class="home">
          <Navigate />
            <div style={{ backgroundColor: "#fff" }} class="container mt-5 mb-5">

                <div class="row">
                    
                    <div onClick={()=>setMode('all')} class={`${generateClass('all')} chip chipWs`}>
                        <img class="circle" src="https://cdn3.vectorstock.com/i/1000x1000/07/67/small-water-bottle-vector-3300767.jpg" alt="Contact Person" />
    All
  </div>
                    <div onClick={()=>setMode('10ltr')} class={`${generateClass('10ltr')} chip chipWs`}>
                        <img src="https://cdn3.vectorstock.com/i/1000x1000/07/67/small-water-bottle-vector-3300767.jpg" alt="Contact Person" />
    10Ltrs
  </div>
                    <div onClick={()=>setMode('5ltr')} class={`${generateClass('5ltr')} chip chipWs`}>
                        <img src="https://cdn3.vectorstock.com/i/1000x1000/07/67/small-water-bottle-vector-3300767.jpg" alt="Contact Person" />
    5Ltrs
  </div>
                    <div onClick={()=>setMode('1ltr')} class={`${generateClass('1ltr')} chip chipWs`}>
                        <img src="https://cdn3.vectorstock.com/i/1000x1000/07/67/small-water-bottle-vector-3300767.jpg" alt="Contact Person" />
    1Ltr
  </div>
                </div>


<Product mode= {mode}/>
            </div>



        </div>
    );
}
function mapStateToProps(myState){
    console.log(myState.authReducer.user);
    return { user: myState.authReducer.user};       //// see consolelog and fincd the crct obj
  }
export default connect(mapStateToProps, actions)(Dashboard);
