import React, {useState, useEffect, useRef} from 'react';
import { useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import * as actions from '../../actions';
import GoogleLogin from 'react-google-login';
import { Link } from 'react-router-dom';
import M from 'materialize-css'

function Water(props){
    let history = useHistory();
    const userRef = useRef();
    const passRef = useRef();


    let routeChange = ()=>{
            let path = `/`;
            history.push(path);
           

    };

    
const responseGoogle = (response) => {
    let googleProfile = {
        name: response.profileObj.givenName,
        mail: response.profileObj.email,
        userId: response.profileObj.googleId,
        dp: response.profileObj.imageUrl
    }
   props.checkUser(googleProfile);
   
  }
    //update
    const mounted = useRef();
useEffect(() => {
  if (!mounted.current) {
    mounted.current = true;
  } else {
    console.log('update');
    try{
        let msg = props.task;
        console.log(props.task.info);
        if(props.task.info === 'Success'){
            M.toast({html: props.task.msg})
            routeChange();

        } else{
            M.toast({html: props.task.msg})
        }
            

    } catch(e){
        console.log('error');
    }
    
  }
});
let normalLogin = ()=>{
  console.log(userRef.current.value)
  if(userRef.current.value.trim() === 'skywalker' && passRef.current.value.trim() === 'asdf'){
    let profile = {
      name: 'marees',
      mail: 'mareesdroid@gmail.com',
      userId: '687ada9853',
      dp: 'test'
  }
 props.checkUser(profile);
  } else if(userRef.current.value.trim() === '' && passRef.current.value.trim() === ''){
    M.toast({html: 'Username or password cannot be empty!'})

  } else{
    M.toast({html: 'User not found try Google login..'})
  }
  
   
};
    return(
        <div style={{
            backgroundColor: "#405C69"
          }} class="full-width col col-lg-6 col-md-6">
           <div class="container">
               <h1 class="white-text">Login</h1>
    <form style={{padding:"5px 25px", backgroundColor:"#EAEFF9"}} >

    
      
         <GoogleLogin className="m-1"
    clientId="661529233526-e260b5p3gdrbklsm0pc877l9rnc7n57v.apps.googleusercontent.com"
    buttonText="Login With Google"
    onSuccess={responseGoogle}
    onFailure={responseGoogle}
  />
         <div style={{height:'20px', position: 'relative'}}>
        <div style={{zIndex: 1, height:'1px', backgroundColor:'#000',width:'100%', position: 'absolute',left:0, top:'50%', bottom:'50%'}}></div>
        <p style={{position: 'absolute', left:'45%' , width:'10%', zIndex: 12,backgroundColor:"#EAEFF9"}} class="center"> OR</p> 
         </div>
     
    <div class="row">

    <div class="input-field">
          <i class="material-icons prefix">account_circle</i>
          <input ref={userRef} type="text" class="validate" />
          <label for="icon_prefix">Username</label>
        </div>
    </div>
        <div class="row">
        <div class="input-field">
          <i class="material-icons prefix">account_circle</i>
          <input ref={passRef} type="password" class="validate" />
          <label for="icon_prefix">Password</label>
        </div>
        </div>

        <a onClick={()=>normalLogin()} style={{backgroundColor:"#405C69"}} class="waves-effect waves-light white-text btn">Login</a>
        
        
     
    </form>
    </div>
           </div>
    );
}
function mapStateToProps(myState){
    console.log(myState.authReducer.task);
    return { task: myState.authReducer.task};       //// see consolelog and fincd the crct obj
  }
export default connect(mapStateToProps, actions)(Water);