import { FETCH_PRODUCTS, UPDATE_PRODUCTS,UPDATE_CART, GET_CART, CHECK_USER, SUCCESS, FAILED, LOGOUT } from '../actions/types'

const authReducer = (state={
    products: [],
    cart:[]
}, action) => {
    let task = {
        msg: action.payload
    };
    switch(action.type){
        case FETCH_PRODUCTS:
            let proData = [];
            console.log(action.payload);
            if(action.payload){
                action.payload.map((data)=>{
                    if(!data.quantity){
                        data.quantity = 0;
                        data.totalPrice = 0;
                    }
                    proData.push(data);
                })
            }
            /// modify quantity here
            state = {
                ...state,  
                products: proData || false
            }
           
            // and return it
            return state;


        case UPDATE_PRODUCTS:
            return state;
        case UPDATE_CART:
            state = {
                ...state,  
                cart: action.payload.cart || false,
                price: action.payload.totalPrice || false
            }
            return state;

        case GET_CART:
            return state;
        case CHECK_USER:
            state = {
                ...state,  
                user: action.payload || false
            }
            return state;
        case SUCCESS:
            task.info = 'Success';
            state = {
                ...state,
                task: task || false
            }
            return state;
        case FAILED:
            task.info = 'Failed';
            state = {
                ...state,
                task: task || false
            }
            return state;

        case LOGOUT:
                state=[];
            return state;
        default:
            console.log('-->>> STEP 1.1: @REDUCER Initial State is assigned to store our state is :');
            console.log('-->>> STEP 1.2: @REDUCER State is sent to store');
            console.log(state)
            return state;
    }
}

export default authReducer;