import React, { useState, useRef, useEffect } from "react";
import { connect } from 'react-redux';
import * as actions from '../../actions';

function Product(props){
    const [isTrigger, setTrigger] = useState(false);
    const [quant, setQuant] = useState(false);
    const [getPos, setPos] = useState();
    
    const myCartQuant = useRef(null);
    let initialQuantz = [];


    let products = [];

  
//     //update
//     const mounted = useRef();
// useEffect(() => {
//   if (!mounted.current) {
//     mounted.current = true;
//   } else {
//     console.log('update');
//     products = props.products;
//    // setHtml(generateProducts());
//   }
// });


useEffect(() => {
    
    try{
        if(props.products.length > 0){
            console.log(props.products)
            return;
        }
            
    } catch(e){
        console.log(e)
    }
   
    props.fetchProducts();
    console.log(props); 
   
   
  }, []);

    let cartChange = (mode, id)=>{
        console.log(id);
     
        
       
        products.map((cart) => {
            
            if(cart._id === id){
                console.log(cart)
                if(mode === 'add'){
                    cart.quantity++;
                    console.log(cart)
                }
                    
                else 
                    if(cart.quantity>0){
                        cart.quantity--;
                        if(cart.quantity === 0){
                            cart.totalPrice = 0;
                        triggerQuant();
                        }
                    } else{
                        cart.totalPrice = 0;
                        triggerQuant();
                    }
                      

                cart.totalPrice = cart.quantity * cart.price;

            }
           // let position;
            //setPos(position);
            setQuant(!quant);
            console.log(products);
           props.updateProducts(products);
           props.updateCart(products);
        });
  
   }

    let triggerCart = (position, id)=>{
        console.log(id);
        cartChange('add', id);
       setTrigger(!isTrigger);
      setPos(position);
      

    }
    let triggerQuant = (position)=>{
       
        setPos(position);
        setQuant(!quant);

    }

  
    
    let generateClass = (position, mode)=>{
      
       
        if(mode === ''){
            if(quant)
                return "hide"
            
        }
        if((position === getPos)){
           
            if(mode === 'cart'){
                return 'hide';
            } else if(mode === ''){
                return 'hide';
            }
               return "show";
        }
        else{
            if(mode === 'cart'){

               return 'show';
            } 
               return "hide"
        }
    }
   
    let generateSpanClass = (quantity)=>{
                if(quantity > 0)
                return 'show';
                else
                return "hide";
         
            
    }
  
    
    let generateProducts = ()=>{
        products = props.products;
        let mode = props.mode;
        let sorted = [];
        try{
            if(mode === 'all')
            sorted = products;
        else
        products.map((pro, position)=>{
            console.log(pro.type);
            if(pro.type === mode)
                sorted.push(pro)
        });
        console.log('test');
        return sorted.map((pro, position)=>{
            return(
                <div  class="card p-2 col-lg-4 col-md-6 col-sm-12">

                <div style={{position:'relative', height:'200px'}} class="m-3 card-body">
                <span class={`${generateSpanClass(pro.quantity)} my-badge white-text badge red badge-${position}`}>{pro.quantity}</span>

                   <img class="product-img card-img-top" src= {pro.img} alt="..." />
                  
                  <div class="product-detail">
                  <h5 class="name-text">{pro.name}</h5>
                  <p class="sub-text">₹{pro.price} - {pro.type}</p>
                  </div>
                  
                  <div class="cart-btn">
                  <a onClick={() => (triggerCart(position, pro._id))} class={`${generateClass(position, 'cart')} bg-dark blue-text d-block btn btn-primary cart-${position}`}>Add To Cart</a>
                  
                 <div className={`${generateClass(position, 'qnt')} add-menu-${position}`}>
                  <div class="justify-content-center d-flex p-3">
                      
                    <a onClick={()=>cartChange('remove', pro._id)}  class="m-1 valign-wrapper col col-lg-3 col-md-3 col-sm-4 waves-effect waves-light btn" >
                    <i class="material-icons tiny">remove</i>
                    </a>
                      <p class={`m-1 btn white black-text text-center center col col-lg-3 col-md-3 col-sm-4 quant-${position}`}>{pro.quantity}</p>
                    <a onClick={()=>cartChange('add', pro._id)} class="m-1 valign-wrapper col col-lg-3 col-md-3 col-sm-4 waves-effect waves-light btn" >
                    <i class="material-icons tiny">add</i>
                    </a>
                    
                  </div>
                  </div>
                   </div>
                   
                   </div>
              </div>
                              
            )
        })
      
        } catch(e){
            return <h1>Oops! Something wen Wrong..</h1>
        }
       
    }

    return(
        
           <div class="row"> 
          {
          generateProducts()
        }
      </div> 
           
    );
}

function mapStateToProps(myState){
    console.log(myState.authReducer.products);
    return { products: myState.authReducer.products};       //// see consolelog and fincd the crct obj
  }
  
  export default connect(mapStateToProps, actions)(Product);