function App(props) {
  useEffect(() => {
   // props.fetchUser();
  });
  return (
    <Router>
  
      {/* A <Switch> looks through its children <Route>s and
          renders the first one that matches the current URL. */}
      <Switch>
       
        <Route exact path="/">
          <Dashboard />
        </Route>
        <Route exact path="/order">
          <Order />
        </Route>
        <Route exact path="/cart">
          <Cart />
        </Route>
        <Route exact path="/login">
          <Main />
        </Route>
      </Switch>
 
  </Router>
    
  );
}

