import React, {useEffect, useState } from 'react';
import M from 'materialize-css'
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import * as actions from '../../actions';
import { useHistory } from 'react-router-dom';
import { IconButton } from '@material-ui/core';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import Badge from '@material-ui/core/Badge';


function Navigate(props){
  let history = useHistory();
  let routeChange = (path)=>{
   
    history.push(path);
   

};
const styles = theme => ({
  badge: {
    top: '50%',
    right: -3,
    // The border color match the background color.
    border: `2px solid ${
      theme.palette.type === 'light' ? theme.palette.grey[200] : theme.palette.grey[900]
    }`,
  },
});
    let instances = null;
    useEffect(() => {
        
      console.log(props);
        let options = {};
       
            var elems = document.querySelector('.sidenav');
            instances = M.Sidenav.init(elems);     
      }, []);
      let closeNav= ()=>{
        console.log('hey');
        if(instances)
        instances.close();
    };
    
    let openNav= ()=>{
       console.log('test');
        if(instances)
        instances.open();
        else{
          var elems = document.querySelector('.sidenav');
          instances = M.Sidenav.init(elems); 
          instances.open();    
        }
    };
    let cartItems =[]; 
    cartItems = props.cart;
    
 
     
     let generateNav = ()=>{

      let isProp = false;
      let dp, name, mail;
      let logOut = ()=>{
        closeNav();
        props.logOut();
        
        routeChange('login');
      };
      try{
        dp = props.user.dp;
        name = props.user.name;
        mail = props.user.mail;
      } catch(err){
        dp = '';
        name = '';
        mail = '';
      }

      return(
        <div>
      <ul id="slide-out" className="sidenav">
  <li><div className="user-view">
    <div className="background blue">
      {/* backgroun */}
      <a><i  style={{float:"right", padding:"15px"}} class="material-icons white">close</i></a>
    </div>
    
    <a  ><img className="circle" src={dp} /></a>
    <a href="#name"><span className="black-text name">{name}</span></a>
    <a href="#email"><span className="black-text email">{mail}</span></a>
  </div></li>
  <Link onClick={()=>closeNav()} to = "/">
  <li><a href="#"><i className="material-icons">cloud</i>Home</a></li>
  </Link>
  <Link onClick={()=>closeNav()} to = "/order">
  <li><a href="#"><i className="material-icons">cloud</i>My Orders</a></li>
  </Link>

  <Link onClick={()=>closeNav()} to = "/cart">
  <li>
  <a><i className="material-icons">cloud</i>Cart</a>
  </li>
  </Link>
 

  <li><div class="divider"></div></li>
  <li><a class="subheader">More</a></li>
  <li><a class="waves-effect" href="#!">Contact</a></li>
  <li><a class="waves-effect" href="#!">Help</a></li>
  <li><a class="waves-effect" onClick={()=>logOut()}>Logout</a></li>

</ul>
<nav class="blue darken-5">
<div class="nav-wrapper">

      <a onClick={()=>openNav()} style={{padding:"0 35px", float:"left"} } class="brand-logo left"><i class="material-icons ">menu</i></a>

      {/* <a onClick={()=>routeChange('cart')} style={{position:'relative', padding:"0 35px", float:"right"} } ><i class="material-icons">shopping_cart</i><span style={{position:'relative',bottom:'0px'}} class="badge badge-light">9</span></a> */}
      <IconButton style={{position:'absolute', top:'10%', right:'2%', bottom: '10%'}} aria-label="cart" onClick={()=>routeChange('cart')}>
      <Badge badgeContent={typeof cartItems === "undefined" ? 0 : cartItems.length} color="secondary">
        <ShoppingCartIcon style={{ color: '#FFFFFF' }} />
      </Badge>
</IconButton>
    </div>
</nav>

           </div>
      );
     }

    return(
        generateNav()
    );
}
function mapStateToProps(myState){
  return {
            products: myState.authReducer.products,
            user: myState.authReducer.user,
            cart: myState.authReducer.cart
          };       //// see consolelog and fincd the crct obj
}

export default connect(mapStateToProps, actions)(Navigate);