import React, { useState, useEffect, useRef } from "react";
import Navigate from "./Navigate";
import * as actions from '../../actions';
import { connect } from 'react-redux';
function Cart(props){
    const [quant, setQuant] = useState(false);

let cartPrice = props.price;
       
//     //update
//     const mounted = useRef();
// useEffect(() => {
//   if (!mounted.current) {
//     mounted.current = true;
//   } else {

//     console.log('update');
//     console.log(props.products);
//    // setHtml(generateProducts());
//   }
// });

let cartItems = [];

    
   

       let cartChange = (mode, id)=>{
        console.log(id);
     
        
       
        cartItems.map((cart) => {
            
            if(cart._id === id){
                console.log(cart)
                if(mode === 'add')
                    cart.quantity++;
                else 
                    if(cart.quantity>0){
                        cart.quantity--;
                    } else{
                        cart.totalPrice = 0;
                    }

            cart.totalPrice = cart.quantity * cart.price;

            }
           // let position;
            //setPos(position);
            setQuant(!quant);
            console.log(cartItems);
           props.updateProducts(cartItems);
           props.updateCart(cartItems);
        });
  
   }

   let generateCart = ()=>{
    cartItems = props.products;
    let sortedCart = [];
    cartItems.map((data, pos)=>{
        if(data.quantity>0)
            sortedCart.push(data);
    })
    cartItems = sortedCart;
    console.log(cartItems)
    if(cartItems.length>0)
        return(
            <div>
            {generateCartItems(cartItems)}

        <div style={{width:'100%'}} class="mt-3 mb-3 btn">Checkout ₹{cartPrice}</div>
            </div>
        );
    else
        return(
            <h1>Cart is Empty</h1>
        );
    
   }

    let generateCartItems= (cart)=>{
        
        return cart.map((data,position)=>{
            return(
                <div class="row justify-content-center">
                <div class="card col-xl-6 col-md-6 col-lg-6 col-sm-12">
                
        <div class="card-image cart-img">
          <img src={data.img} />
         
        </div>
        <div style={{float:'right'}} class="card-content">
            <div class="contents">
        <div style={{width:'50%'}} class="head-content center">
                <h6 class="name-text center">{data.name}</h6>
            <h7 class="sub-text center">{data.type}</h7>
                </div>
       

            <div style={{width:'50%'}} class="my-holder center">
              
          
                <div className="add-menu">
                <div class="btn-group" role="group" aria-label="...">
                <button onClick={()=>cartChange('remove', data._id)} type="button" class="btn btn-lg btn-secondary">-</button>
            <button type="button" class="btn text-dark btn-lg bg-white btn-secondary">{data.quantity}</button>
                <button onClick={()=>cartChange('add', data._id)} type="button" class="btn btn-lg btn-secondary">+</button>

                </div>
</div>
            <h4 class="text-center">₹{data.totalPrice}</h4> 
                </div>
           
                </div>
        </div>
        
      </div>

      
              
                 </div>
               
            );
        });
    }
   return(
       <div>
           <Navigate />
           <div class="my-container container mt-5">
               {generateCart()}
                         
             

                    
      </div>
       </div>
   )
}
function mapStateToProps(myState){
    console.log(myState.authReducer.products);
    return { products: myState.authReducer.products,
        price: myState.authReducer.price

    };       //// see consolelog and fincd the crct obj
  }
  
  export default connect(mapStateToProps, actions)(Cart);
